<template>
  <div class="select-range row">
    <BaseSelect
      label="от"
      v-model="currentValue[0]"
      v-bind:options="options"
    ></BaseSelect>
    <BaseSelect
      label="до"
      v-model="currentValue[1]"
      v-bind:options="options"
    ></BaseSelect>
  </div>
</template>

<script>
import BaseSelect from "./BaseSelect";

export default {
  name: "BaseSelectRange",
  components: {
    BaseSelect
  },
  props: ["value", "min", "max", "step"],
  data() {
    return {
      currentValue: this.value,
      options: []
    };
  },
  created() {
    for (let i = this.min; i < this.max; i += this.step) {
      let option = Math.round(i * 10) / 10;
      this.options.push(option);
    }

    if (!this.options.includes(this.max)) {
      this.options.push(this.max);
    }
  },
  watch: {
    value(newValue) {
      this.currentValue = newValue;
    },
    currentValue(newValue) {
      this.$emit("input", newValue);
    }
  }
};
</script>
